<script>
	import setSmoll from './settingSomll.vue'
	import settingHelp from './settingHelp.vue'
	import settingTerms from './settingTerms.vue'
	
	export default {
		components:{setSmoll,settingHelp,settingTerms},  
		
		data() {
			return {
				istab:true,//这些都要复制到需要的地方
				cat:true, 
				cats:false, 
				catUs:false,
				catHelp:false,
				catServices:false,
				Privacy:false,
				Notifications:false,
				logOut:false,
				//About Us 
				id:'',
				ifGo:false,
				content:'',
				key:'',
				title:''
			}
		},
		onLoad() {
	
		}, 
		onShow() {
			// let that = this
			// that.$utils.reloadCurrentPage(that);

		},
		methods: {
			goLogin(){
				uni.reLaunch({
					url:'../welcomePage/inputEmail'
				})
			},
			newonShow(title,content,key){
				this.ifGo = true
				this.title = title
				this.content = content
				this.key = key
			},
			
			newclose(){
				this.ifGo = false
			},
			async saoYisao(){
				let that = this
				let detaile = ''
				let isLogin = uni.getStorageSync('isLogin')
				if(!isLogin){
					// console.log('未登录');
					this.newonShow('Please Login',"You haven't logged in yet. Please log in",'Login')
				}else{
					// console.log('已登录');
					uni.scanCode({
						onlyFromCamera: true,                   
						success(res) {  
							console.log(res);
							detaile = res.result
							if(detaile.startsWith('https://www.tekygo.com')){
								uni.redirectTo({
									url:'../subscription/subscription'
								}) 
								// that.$refs.setting.openLoad()
								// that.getUrl()
								// console.log(res);
								//获取二维码内容开头为https://www.tekygo.com
								console.log('二维码');
							}else if(detaile.length == 32){
								//设备id
								console.log('设备id');
								that.id = res.result
								uni.setStorageSync("id",res.result)
								// console.log(that.id);
								that.scanCodes()
							}else{
								this.$refs.setBind.openPopup('Invalid code')
							}
						}
					})
				}
			
			},
			async getUrl(){
				const res = await this.$myRequest({
					url:'/api/sub/get'
				})
				console.log(res);
				console.log(res.data.succeeded);
				if(res.data.succeeded){
					this.$refs.setting.closeLoad()
					console.log('成功了');
					plus.runtime.openURL(res.data.data);
				}else{
					this.$refs.setBind.openPopup(res.data.errmsg)
				}
			},
			
			//扫码绑定设备
			async scanCodes(){
				// var that = this
				const res = await this.$myRequest({
					url:'/api/device/bind'+'?processId='+ this.id,
				})
				console.log(res); 
				if(res.data.succeeded){
					console.log('绑定成功');
					this.$refs.setBind.openPopup('Binding success!')
					uni.setStorageSync("isBind",true)
					this.bindSyntony()
				}else{
					console.log('绑定失败');
					this.$refs.setBind.openPopup(res.data.errmsg)
				}
			}, 
			
			async bindSyntony(){
				const res = await this.$myRequest({
					url:'/api/device/list' + '?page=1' +'&limit=1',
				})
				console.log('展示');
				console.log(res)
				console.log(res.data.data.records[0].serial);
				uni.setStorageSync("serial",res.data.data.records[0].serial)
				// this.serial = res.data.data.records[0].serial
			},  
			closePopup(){
				this.logOut = false
			},
			closePopupsss(){
				uni.removeStorageSync('Password')
				uni.removeStorageSync('id') 
				uni.removeStorageSync('token')
				uni.removeStorageSync('isBind')
				uni.removeStorageSync('hasActive')
				uni.reLaunch({
					url:'../welcomePage/inputEmail'
				})
			},
			goAboutUs(){
				this.cat = false
				this.catUs = true 
				this.cats = true
			},
			goBack(){
				this.cat = true
				this.cats = false
				this.catUs = false 
				this.catHelp = false
				this.catServices = false
				this.Privacy = false
				this.Notifications=false
			},
			goHelp(){
				this.cat = false
				this.catHelp = true 
				this.cats = true
			},	
			goServices(){
				this.cat = false
				this.catServices = true 
				this.cats = false
			},
			goPrivacy(){
				this.$refs.setNoti.openPopup('Cannot Open') 
			},
			goNotifications(){
				this.$refs.setNoti.openPopup('Not Correct Info')
			},
			logOuts(){
				this.logOut = true
			}
		}
	}
</script>

